@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Text&display=swap);
* {
  line-height: 1em;
  padding: 0;
  margin: 0;
  font-family: 'Darker Grotesque', sans-serif;
}

p {
  letter-spacing: 0.75px;
}

h1 {
  font-family: 'DM Serif Text', serif;
  font-weight: 400;
}

html {
  width: 100vw;
  background-color: rgb(250, 250, 250);
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  width: 100%;
  position: relative;
}

a {
  text-decoration: none;
  color: black;
}

button {
  border: none;
  padding: 10px;
  border-radius: 15px;
  font-size: 20px;
  cursor: pointer;
  transition: 0.2s ease;
}

button:hover {
  background-color: rgb(54, 54, 54);
  color: rgb(245, 245, 245);
}

.visible {
  opacity: 1;
  transform: translateY(0);
  transition: 0.6s ease;
}

.page-leave {
  opacity: 0;
  transform: translateY(-40px);
}

.nav-container h1 {
    font-family: 'Darker Grotesque', sans-serif;
    font-weight: 700;
}

.nav-container {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    width: 100%;
    overflow: hidden;
}

.page-leave {
    opacity: 0;
    transform: translateX(-25px);
}

.home-icon {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    z-index: 2;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
    border-bottom: 1px solid rgb(238, 238, 238, 0.3);
}

.home-icon img {
    width: 40px;
    aspect-ratio: 1;
    padding: 10px 15px;
    z-index: 5;
}

.nav-burger-container {
    top: 0;
    right: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60px;
    grid-gap: 8.5px;
    gap: 8.5px;
    aspect-ratio: 1;
    border-radius: 25px;
    cursor: pointer;
    z-index: 3;
    transition: 0.5s ease;
}

.burger1, .burger2, .burger3 {
    width: 50%;
    height: 2px;
    background-color: black;
    transition: 0.5s ease-in-out;
    transform-origin: 0% 0%;

}

.burgerAnimation1, .burgerAnimation3, .burgerAnimation {
    background-color: rgb(255, 236, 153);
}

.burgerAnimation1 {
    transform: rotate(45deg);
}

.burgerAnimation3 {
    transform: rotate(-45deg);
}

.burgerAnimation {
    opacity: 0;
}

.nav-link-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    background-color: rgb(48, 48, 48);
    opacity: 0;
    transition: 0.5s ease;
    grid-gap: 1em;
    gap: 1em;
    pointer-events: none;
}

.navVisible {
    opacity: 1;
    pointer-events: all;
}

.nav-links {
    transition: 0.5s ease-in-out;
    color: white;
    font-size: 36px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.icon-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
    width: 100%;
    padding: 10px 20px 0 0;
    transition: 0.5s ease-in-out;
    overflow: hidden;
}

.itemVisible {
    transform: translateX(-25px);
    opacity: 1;
}

.icons {
    font-size: 36px;
    color: white;
}

h1 {
    font-size: 50px;
}

.projects-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 90%;
    grid-gap: 10%;
    gap: 10%;
}

.projects-drop-arrow {
    color: white;
    font-size: 30px;
    transition: 2s cubic-bezier(.76,.01,.57,.99);
    opacity: 0;
    padding: 10px 0 0 0;
    transform: translateY(-45px);
    pointer-events: none;
    transition: 1s ease;
}

.drop-active {
    transform: translateY(0);
    pointer-events: all;
    opacity: 1;
}

.projects-sub-div {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    pointer-events: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    grid-gap: 5px;
    gap: 5px;
    height: 0;
    opacity: 0;
    transition: height 1s ease;
}

.projects-sub-div-desktop {
    position: absolute;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    pointer-events: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    grid-gap: 5px;
    gap: 5px;
    opacity: 0;
}

.projects-div-visible {
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    opacity: 1;
    transition: 1s ease;
    pointer-events: all;
}

.projects-desk-visible {
    display: none;
    pointer-events: all;
    opacity: 1;
    position: relative;
}

.projects-sub-div a {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content
}

.projects-sub-div a h1 {
    font-size: 36px;
    padding: 0;
    margin: 0;
}

@media only screen and (min-width: 768px) {
    .home-icon img {
        width: 60px;
    }

    .nav-burger-container {
        width: 80px;
        grid-gap: 12px;
        gap: 12px
    }

    .burger1, .burger2, .burger3 {
    }

    h1 {
        font-size: 70px;
    }

    .projects-drop-arrow {
        font-size: 70px;
    }

    .projects-sub-div .nav-links h1 {
        font-size: 50px;
    }
}

@media only screen and (min-width: 1366px) {
    .home-icon {
        justify-content: center;
        z-index: 2;
        pointer-events: all;
        background-color: transparent;
        -webkit-backdrop-filter: blur(4px);
                backdrop-filter: blur(4px);
        cursor: pointer;
        transition: 0.5s ease;
    }

    .navHome {
        pointer-events: visible;
    }

    .nav-link-container{
        opacity: 1;
        flex-direction: row;
        pointer-events: all;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        align-items: center;
        grid-gap: 5%;
        gap: 5%;
        z-index: 3;
        background-color: transparent;
        pointer-events: none;
    }

    .nav-links, .projects-drop-arrow {
        color: black;
        opacity: 1;
        transform: rotate(0);
        pointer-events: all;
        width: 20%;
        padding: 0;
        transition: 0.5s ease;
        cursor:pointer;
    }

    .nav-links, .projects-drop-arrow:hover {
        animation: arrowPopHeader 1s cubic-bezier(0,.76,.14,.99);
    }

    .nav-links {
        width: 15%;
        pointer-events: auto;
        white-space: nowrap;
    }
    
    .nav-links h1 {
        font-size: 30px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        transition: 0.5s ease;
        padding: 5px;
        border-radius: 5px;
        position: relative;
    }

    .nav-links h1::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: rgb(186, 108, 238);
        left: 0;
        bottom: 0;
        transition: 0.4s ease;
        transform: scale(0);
    }

    .nav-links h1:hover::after {
        transform: scale(1);
        animation: rotate 0.2s ease;
    }

    .nav-link-container .nav-links h1:hover {
        background-color: rgb(240, 240, 240);
        margin: 5px 10px;
    }

    .nav-link-container .projects-container .nav-links h1:hover {
        margin: 0;
        background-color: rgb(240, 240, 240);
    }

    .nav-resume{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin: 0 0 0 5%;
        width: 10%;
    }

    .nav-about {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin: 0 5% 0 0;
        width: 10%;
    }

    .projects-container {
        grid-gap: 0;
        gap: 0;
        width: 100%;
    }

    .projects-container {
        justify-content: center;
        position: relative;
        width: 15%;
    }

    .projects-container .projects-sub-div-desktop .nav-links h1 {
        white-space: nowrap;
    }

    .projects-title {
        padding: 0;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .projects-sub-div-desktop {
        display: flex;
        position: absolute;
        align-items: flex-start;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        pointer-events: none;
        right: 0;
        bottom: -300%;
        border-radius: 15px;
        padding: 10px 10%;
        -webkit-backdrop-filter: blur(15px);
                backdrop-filter: blur(15px);
        border: 1px solid rgb(228, 228, 228);
        transform: translateY(-35px);
        transition: 0.5s ease;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .projects-sub-div-desktop .nav-links {
        pointer-events: none;
    }

    .projects-sub-div {
        pointer-events: none;
        display: none;
    }

    .projects-div-visible {
        display: none;
    }

    .projects-desk-visible {
        display: flex;
        pointer-events: all;
        transform: translateY(0);
    }

    .projects-desk-visible .nav-links {
        pointer-events: all;
    }

    .nav-burger-container {
        display: none;
    }

    .icon-container {
        display: none;
    }

    .icon-container a .icons {
        padding: 0;
        font-size: 40px;
        color: black;
    }

    .itemVisible {
        transform: translateX(0);
    }
}

@media only screen and (min-width: 1920px) {
    .projects-sub-div-desktop {
        right: 10%;
    }
}

@keyframes arrowPopHeader {
    100%, 0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-5px);
    }
}
.page-leave {
    opacity: 0;
    transform: translateY(-40px);
  }

.home-container {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.5s ease;
    margin: 10vh 0 0 0;
}

.home-1 {
    height: 90vh;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: left;
    position: relative;
}

.home-1-text p {
    font-size: 20px;
}

.home-1-art {
    width: 100%;
    aspect-ratio: 1;
    background-color: rgba(233, 233, 233, 0.4);
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
    border-radius: 15px;
    transition: 1s ease;
    z-index: -1;
    position: relative;
    overflow: hidden;
}

.head {
    width: 70%;
    aspect-ratio: 1;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url(/portfolio/static/media/overlay.399bf960.png),  radial-gradient(circle, rgba(222,163,237,0.5) 0%, rgba(232,202,239,0.5) 57%, rgb(245, 230, 252) 68%, rgb(249, 246, 252) 80%), linear-gradient(90deg, rgb(209, 105, 238) 0%, rgb(255, 255, 255) 100%);
    animation: headSpin 6s infinite ease;
    transform-origin: center;
}

.eye1, .eye2 {
    background-color: rgb(255, 255, 255);
    width: 5%;
    aspect-ratio: 1;
    border-radius: 50%;
    position: absolute;
}

.eye1 {
    top: 50%;
    left: 40%;
    transform: translate(-40%, -50%);
    animation: eye1Anim 6s infinite ease;
}

.eye2 {
    top: 50%;
    left: 60%;
    transform: translate(-60%, -50%);
    animation: eye2Anim 6s infinite ease;
}

.hat-main {
    width: 35%;
    aspect-ratio: 2/1;
    position: relative;
    transform: rotate(-37deg);
    animation: hatAnim 6s infinite cubic-bezier(.54,0,.44,1);
}

.hat-top {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    height: 100%;
    width: 75%;
    background: url(/portfolio/static/media/overlay.399bf960.png), linear-gradient(90deg, rgba(255,255,255,1) 0%, rgb(58, 58, 58) 23%, rgba(79,79,79,1) 77%, rgba(255,255,255,1) 100%);
}

.hat-bot {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 40%;
    background: url(/portfolio/static/media/overlay.399bf960.png), radial-gradient(circle, rgb(70, 70, 70) 0%, rgb(85, 85, 85) 57%, rgb(241, 241, 241) 100%);
}

.pupil {
    position: absolute;
    width: 25%;
    aspect-ratio: 1;
    background: url(/portfolio/static/media/overlay.399bf960.png),  radial-gradient(circle, rgb(70, 70, 70) 0%, rgb(85, 85, 85) 57%, rgb(241, 241, 241) 68%, rgb(119, 119, 119) 80%);
    background-size: cover;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    animation: pupilAnim infinite 6s ease;
    transform-origin: center;
}

@keyframes headSpin {
    0% {
        transform: translate(-50%, -50%) rotate(0);
        left: 50%;
    }
    10% {
        transform: translate(-50%, -50%) rotate(3deg);
        left: 55%;
    }
    25% {
        transform: translate(-50%, -50%) rotate(3deg);
        left: 55%;
    }
    30% {
        transform: translate(-50%, -50%) rotate(-3deg);
        left: 45%;
    }
    60% {
        transform: translate(-50%, -50%) rotate(-3deg) scale(1);
        left: 45%;
    }
    70% {
        transform: translate(-50%, -50%) rotate(0deg) scale(1.3);
        left: 50%;
    }
    85% {
        transform: translate(-50%, -50%) rotate(0deg) scale(1.3);
        left: 50%;
    }
    100% {
        transform: translate(-50%, -50%) rotate(0);
        left: 50%;
    }
}

@keyframes hatAnim {
    0% {
        transform: rotate(-37deg);
    }
    60% {
        transform: rotate(-37deg) translateY(0);
    }
    66% {
        transform: rotate(-37deg) translateY(-55px);
    }
    97% {
        transform: rotate(-37deg) translateY(0);
    }
    100% {
        transform: rotate(-37deg);
    }
}

@keyframes eye1Anim {
    0% {
        transform: translate(-40%, -50%) scaleY(0);
        left: 40%;
    }

    5% {
        transform: translate(-40%, -50%) scaleY(1);
        left: 50%;
    }

    25% {
        transform: translate(-40%, -50%) scaleY(1);
        left: 50%;
    }

    30% {
        transform: translate(-40%, -50%) scaleY(0) scaleX(0);
        border-radius: 50%;
        left: 30%;
    }

    35% {
        transform: translate(-40%, -50%) scaleY(0.5) scaleX(2);
        border-radius: 15%;
        left: 30%;
    }

    60% {
        transform: translate(-40%, -50%) scaleY(0.5) scaleX(2);
        border-radius: 15%;
        left: 30%;
    }

    65% {
        transform: translate(-40%, -50%) scaleY(0) scaleX(0);
        border-radius: 50%;
        left: 20%;
    }

    70% {
        transform: translate(-40%, -50%) scaleY(6) scaleX(4);
        left: 20%;
    }

    80% {
        transform: translate(-40%, -50%) scaleY(5) scaleX(5);
        left: 20%;
    }

    95% {
        transform: translate(-40%, -50%) scaleY(5) scaleX(5);
        left: 20%;
    }

    100% {
        transform: translate(-40%, -50%) scaleY(0) scaleX(0);
        left: 40%;
    }
}

@keyframes eye2Anim {
    0% {
        transform: translate(-40%, -50%) scaleY(0);
        left: 60%;
    }

    5% {
        transform: translate(-40%, -50%) scaleY(1);
        left: 70%;
    }

    25% {
        transform: translate(-40%, -50%) scaleY(1);
        left: 70%;
    }

    30% {
        transform: translate(-40%, -50%) scaleY(0);
        border-radius: 50%;
        left: 50%;
    }

    35% {
        transform: translate(-40%, -50%) scaleY(0.5) scaleX(2);
        border-radius: 15%;
        left: 50%;
    }

    60% {
        transform: translate(-40%, -50%) scaleY(0.5) scaleX(2);
        border-radius: 15%;
        left: 50%;
    }

    65% {
        transform: translate(-40%, -50%) scaleY(0) scaleX(0);
        border-radius: 50%;
        left: 80%;
    }

    70% {
        transform: translate(-40%, -50%) scaleY(6) scaleX(4);
        right: 80%;
    }

    80% {
        transform: translate(-40%, -50%) scaleY(5) scaleX(5);
        left: 80%;
    }

    95% {
        transform: translate(-40%, -50%) scaleY(5) scaleX(5);
        left: 80%;
    }

    100% {
        transform: translate(-40%, -50%) scaleY(0) rotate(0);
        left: 60%;
    }
}

@keyframes pupilAnim {
    0% {
        width: 0%;
    }

    65% {
        width: 0%;
        left: 0;
        top: 0;
        transform: rotate(0);
    }

    70% {
        width: 100%;
        left: 0;
        top: 0;
        transform: rotate(180deg);
    }

    95% {
        width: 100%;
        left: 0;
        top: 0;
        transform: rotate(360deg);
    }

    100% {
        width: 0%;
        left: 0;
        top: 0;
        transform: rotate(360deg);
    }
}

.art-click {
    pointer-events: none;
}

.button-arr {
    padding: 20px 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
}

.real-button {
    filter: drop-shadow(0px 1px 1px rgb(141, 141, 141));
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.button-link {
    position: absolute;
    right: 0;
    width: 50%;
    height: 48px;
    transition: 0.3s ease;
}

.invisible {
    opacity: 0;
    background-color: rgb(78, 78, 78);
    pointer-events: none;
}

.icons, .icons-1, .icons-2, .icons-3 {
    color: black;
    cursor: pointer;
    margin: 5px 0 0 0px;
    padding: 5px;
    background-color: rgb(240, 240, 240);
    border-radius: 5px;
    font-size: 36px;
    filter: drop-shadow(0px 1px 1px rgb(141, 141, 141))
}

.icons {
    transform: rotate(0deg);
    transition: 0.5s ease;
}

.icons-1 {
    opacity: 0;
    z-index: -1;
    transition: 0.75s ease;
}

.icons-2 {
    opacity: 0;
    z-index: -1;
    transition: 1s ease;
}

.icons-3 {
    opacity: 0;
    z-index: -1;
    transition: 1.25s ease;
}

.icon-click {
    background-color: rgb(66, 66, 66);
    color: white;
    transform: rotate(-90deg);
    animation: mainArrowPop 1s cubic-bezier(0,.79,.25,.99);
}

.icon-click-1 {
    opacity: 1;
    background-image: linear-gradient(45deg, rgb(250, 216, 143) 0%, rgb(235, 235, 235) 100%);
    animation: arrowPop 1.2s cubic-bezier(0,.79,.25,.99), pulse 2.6s infinite ease;
    z-index: 1;
}

.icon-click-2 {
    opacity: 1;
    background-color: rgb(114, 181, 226);
    animation: arrowPop 1.4s cubic-bezier(0,.79,.25,.99), pulse 3s infinite ease;
    z-index: 1;
}

.icon-click-3 {
    opacity: 1;
    background-color: rgb(118, 230, 118);
    animation: arrowPop 1.6s cubic-bezier(0,.79,.25,.99), pulse 3.4s infinite ease;
    z-index: 1;
}

@keyframes pulse {
    100%, 0% {
        opacity: 0.7
    }
    50% {
        opacity: 1;
    }
}

@keyframes mainArrowPop {
    0% {
        transform: rotate(0)
    }
    100% {
        transform: rotate(-90deg)
    }
}

@keyframes arrowPop {
    100%, 0% {
        transform: translateY(0)
    }
    50% {
        transform: translateY(-20px)
    }
}

